"use client";

import { useEffect, useState, use } from "react";
import { notFound, useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { Label } from "~/components/ui/label";
import { Textarea } from "~/components/ui/textarea";
import { useToast } from "~/hooks/use-toast";
import { Checkbox } from "~/components/ui/checkbox";

interface Props {
    params: Promise<{
        teamId: string;
    }>;
}

interface Player {
    id: number;
    name: string;
    isTeamLeader: boolean;
}

interface Game {
    id: number;
    name: string;
    description: string;
}

interface GameInstance {
    id: number;
    startedAt: string;
    completedAt: string | null;
    currentPuzzleOrder: number;
}

interface Booking {
    id: number;
    code: string;
    createdAt: string;
    expiresAt: string | null;
    game: {
        id: number;
        name: string;
    };
    instance: GameInstance | null;
}

interface Team {
    id: number;
    name: string;
    players: Player[];
    bookings: Booking[];
}

type TeamResponse = {
    team: Team;
};

type GameResponse = {
    games: Game[];
};

type BookingResponse = {
    booking: Booking;
};

function EditTeamPageContent({ teamId }: { teamId: number }) {
    const [team, setTeam] = useState<Team | null>(null);
    const [games, setGames] = useState<Game[]>([]);
    const [loading, setLoading] = useState(true);
    const [submitting, setSubmitting] = useState(false);
    const { toast } = useToast();
    const router = useRouter();

    useEffect(() => {
        if (isNaN(teamId)) {
            notFound();
            return;
        }

        async function fetchData() {
            try {
                const [teamResponse, gamesResponse] = await Promise.all([
                    fetch(`/api/teams/${teamId}`),
                    fetch('/api/games')
                ]);

                if (!teamResponse.ok) throw new Error('Failed to fetch team');
                if (!gamesResponse.ok) throw new Error('Failed to fetch games');

                const teamData = await teamResponse.json() as TeamResponse;
                const gamesData = await gamesResponse.json() as GameResponse;

                setTeam(teamData.team);
                setGames(gamesData.games);
            } catch (error) {
                console.error('Error fetching data:', error);
                notFound();
            } finally {
                setLoading(false);
            }
        }

        void fetchData();
    }, [teamId]);

    async function createBooking(gameId: number) {
        setSubmitting(true);
        try {
            const response = await fetch('/api/bookings', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    teamId,
                    gameId,
                }),
            });

            if (!response.ok) {
                throw new Error('Failed to create booking');
            }

            const data = (await response.json()) as BookingResponse;
            toast({
                title: "Success",
                description: `Booking created! Code: ${data.booking.code}`,
            });

            // Refresh the page data
            const teamResponse = await fetch(`/api/teams/${teamId}`);
            if (!teamResponse.ok) throw new Error('Failed to fetch updated team data');
            const teamData = (await teamResponse.json()) as TeamResponse;
            setTeam(teamData.team);
        } catch (error) {
            console.error('Error creating booking:', error);
            toast({
                variant: "destructive",
                title: "Error",
                description: "Failed to create booking",
            });
        } finally {
            setSubmitting(false);
        }
    }

    if (loading) {
        return <div>Loading team...</div>;
    }

    if (!team) {
        return null;
    }

    return (
        <div className="space-y-8">
            <div className="mb-8">
                <h1 className="text-3xl font-bold tracking-tight">Edit Team</h1>
                <p className="text-slate-400">Update team details and manage players.</p>
            </div>

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle>Team Details</CardTitle>
                </CardHeader>
                <CardContent>
                    <form
                        onSubmit={async (e) => {
                            e.preventDefault();
                            const formData = new FormData(e.currentTarget);
                            const teamName = formData.get('teamName') as string;

                            // Collect all player data
                            const players = team.players.map((_, index) => ({
                                name: formData.get(`players[${index}].name`) as string,
                                isTeamLeader: formData.get(`players[${index}].isTeamLeader`) === 'on',
                            }));

                            try {
                                const response = await fetch(`/api/teams/${teamId}`, {
                                    method: 'PUT',
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        name: teamName,
                                        players
                                    }),
                                });

                                const data = await response.json() as { error?: string; team?: Team };

                                if (!response.ok) {
                                    throw new Error(data.error ?? 'Failed to update team');
                                }

                                toast({
                                    title: "Success",
                                    description: "Team updated successfully",
                                });

                                router.refresh();
                            } catch (error) {
                                console.error('Error updating team:', error);
                                toast({
                                    variant: "destructive",
                                    title: "Error",
                                    description: error instanceof Error ? error.message : "Failed to update team",
                                });
                            }
                        }}
                        className="space-y-6"
                    >
                        <input type="hidden" name="teamId" value={team.id} />

                        <div className="space-y-2">
                            <Label htmlFor="teamName">Team Name</Label>
                            <Input
                                id="teamName"
                                name="teamName"
                                defaultValue={team.name}
                                required
                                className="bg-slate-950"
                            />
                        </div>

                        <div className="space-y-4">
                            <Label>Players</Label>
                            <div className="space-y-4">
                                {team.players.map((player, index) => (
                                    <div key={player.id} className="p-4 rounded-lg border border-slate-800 bg-slate-900/50">
                                        <div className="flex flex-wrap items-center gap-4">
                                            <div className="flex-1 min-w-[200px]">
                                                <Label htmlFor={`player-${index}-name`} className="sr-only">Name</Label>
                                                <Input
                                                    id={`player-${index}-name`}
                                                    name={`players[${index}].name`}
                                                    defaultValue={player.name}
                                                    placeholder="Player name"
                                                    required
                                                    className="bg-slate-950"
                                                />
                                            </div>
                                            <div className="flex-1 min-w-[200px]">
                                                <Label htmlFor={`player-${index}-email`} className="sr-only">Email</Label>
                                                <Input
                                                    id={`player-${index}-email`}
                                                    name={`players[${index}].email`}
                                                    type="email"
                                                    placeholder="Email"
                                                    required
                                                    className="bg-slate-950"
                                                />
                                            </div>
                                            <div className="flex-1 min-w-[200px]">
                                                <Label htmlFor={`player-${index}-phone`} className="sr-only">Phone</Label>
                                                <Input
                                                    id={`player-${index}-phone`}
                                                    name={`players[${index}].phone`}
                                                    type="tel"
                                                    placeholder="Phone"
                                                    className="bg-slate-950"
                                                />
                                            </div>
                                            <div className="flex items-center space-x-2">
                                                <Checkbox
                                                    id={`player-${index}-leader`}
                                                    name={`players[${index}].isTeamLeader`}
                                                    defaultChecked={player.isTeamLeader}
                                                />
                                                <Label
                                                    htmlFor={`player-${index}-leader`}
                                                    className="font-normal whitespace-nowrap"
                                                >
                                                    Is team leader
                                                </Label>
                                            </div>
                                            <Button
                                                type="button"
                                                variant="destructive"
                                                onClick={() => {
                                                    setTeam(prev => {
                                                        if (!prev) return prev;
                                                        return {
                                                            ...prev,
                                                            players: prev.players.filter((_, i) => i !== index)
                                                        };
                                                    });
                                                }}
                                                className="h-8"
                                            >
                                                Delete
                                            </Button>
                                        </div>
                                    </div>
                                ))}
                                <Button
                                    type="button"
                                    variant="outline"
                                    onClick={() => {
                                        setTeam(prev => {
                                            if (!prev) return prev;
                                            return {
                                                ...prev,
                                                players: [
                                                    ...prev.players,
                                                    {
                                                        id: Math.random(), // Temporary ID for new player
                                                        name: '',
                                                        isTeamLeader: false,
                                                    }
                                                ]
                                            };
                                        });
                                    }}
                                    className="w-full"
                                >
                                    Add Player
                                </Button>
                            </div>
                        </div>

                        <div className="flex justify-end gap-4">
                            <Button
                                type="button"
                                variant="outline"
                                onClick={() => window.history.back()}
                            >
                                Cancel
                            </Button>
                            <Button type="submit">
                                Save Changes
                            </Button>
                        </div>
                    </form>
                </CardContent>
            </Card>

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle>Game Bookings</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="space-y-6">
                        <div>
                            <h3 className="text-lg font-medium mb-4">Current Bookings</h3>
                            {team.bookings.length === 0 ? (
                                <p className="text-slate-400">No bookings yet.</p>
                            ) : (
                                <div className="space-y-4">
                                    {team.bookings.map((booking) => (
                                        <div
                                            key={booking.id}
                                            className="flex items-center justify-between p-4 rounded-lg border border-slate-700 bg-slate-800/50"
                                        >
                                            <div>
                                                <h4 className="font-medium">{booking.game.name}</h4>
                                                <p className="text-sm text-slate-400">Code: {booking.code}</p>
                                                <p className="text-sm text-slate-400">
                                                    Created: {new Date(booking.createdAt).toLocaleDateString()}
                                                </p>
                                            </div>
                                            {booking.instance ? (
                                                <span className="text-sm text-green-400">Game Started</span>
                                            ) : (
                                                <span className="text-sm text-yellow-400">Not Started</span>
                                            )}
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>

                        <div>
                            <h3 className="text-lg font-medium mb-4">Create New Booking</h3>
                            <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
                                {games.map((game) => (
                                    <div
                                        key={game.id}
                                        className="p-4 rounded-lg border border-slate-700 bg-slate-800/50"
                                    >
                                        <h4 className="font-medium mb-2">{game.name}</h4>
                                        <p className="text-sm text-slate-400 mb-4">{game.description}</p>
                                        <Button
                                            onClick={() => createBooking(game.id)}
                                            disabled={submitting}
                                            className="w-full"
                                        >
                                            {submitting ? "Creating..." : "Create Booking"}
                                        </Button>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}

export default function EditTeamPage({ params }: Props) {
    const resolvedParams = use(params) as { teamId: string };
    const teamId = parseInt(resolvedParams.teamId);
    if (isNaN(teamId)) {
        notFound();
    }
    return <EditTeamPageContent teamId={teamId} />;
} 